.App {

  margin: 0px auto;
}

h1{
  text-align: center;
  margin-top: 50px;
}

.mimg{
  width: 100%;
}

h3{
  text-align: center;
}

.footer{
  border-top: solid 2px gray;
  margin-top: 100px;
}

.menu{
  margin:  0px auto;
  width: 80%;
  padding: 10px 0px 10px 100px;
 
  border-bottom: double 4px gray;
}

.menu a{
  padding-right: 30px;
  font-size: 20px;
  color: black;
  text-decoration: none;
  font-weight: bold;
}

.menu a:hover{
  color: pink;
}

.login{
  margin: 0px auto;

  width: 20%;
  text-align: center;
  padding: 20px;
  border-radius: 20px;
}

.login input[type='text']{
  margin-top: 20px;
}

.login input{
  width: 90%;
  padding: 10px;
  margin-bottom: 10px;
  border: none;
  border-bottom: solid 2px gray;
  font-size: 18px;
}

.login input:focus{
  outline: none;
  border-bottom: solid 2px black;
}

.logBtn{
  background-color: rgb(215, 236, 255);
  border-style: none;
  padding: 5px 10px;
  border-radius: 5px;
  font-weight: bold;
  margin-left: 5px;
  cursor: pointer;
  margin-top: 20px;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 15px;
}

.logBtn:hover{
  background-color: rgb(255, 219, 225);
}

.email{
  font-family: 'Noto Sans KR', sans-serif;
  float:right;
  font-weight: bold;
  padding-right: 20px;
  color: palevioletred;
}

.page{
  width: 20%;
  margin: 0px auto;
}

.page input{
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
}

.update{
  background-color: rgb(215, 236, 255);
  border-style: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  padding: 5px 10px;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 15px;
  margin-bottom: 50px;
}

.users{
  width: 60%;
  margin: 0px auto;
  text-align: center;
}

.user{
  overflow: hidden;
  margin: 0px auto;
  border-bottom: 1px dotted gray;
}

.photo{
  width: 50%;
  float: left;
  margin-top: 20px;
}

.info{

  float: left;
}
